.wrap{
    background-color: white;
    max-width: 366px;
    width: 100%;
    border-radius: 15px;
    height: fit-content;
    padding: 12px 28px;
    &__item:first-child{
        .title{
            color: var(--main_black);
            text-align: center;
            font-family: Roboto;
            font-size: 20px;
            font-weight: 600;
            letter-spacing: 0.6px;
            width: 100%;
            display: block;
            margin-bottom: 5px;
        }
        input[type="range"] {
            width: 100%;
            height: 8px;
            appearance: none;
            border: 1px solid var(--main_black);
            border-radius: 5px;
            padding: 0 4px;
            margin-bottom: 12px;
            &::-webkit-slider-thumb {
                appearance: none;
                width: 14px;
                height: 14px;
                background-color: #ffffff;
                border: 2px solid var(--main_black);
                border-radius: 50%;
                cursor: pointer;
              }
        }
    }
    &__item:last-child{
        .block{
            display: flex;
            justify-content: space-between;
        }
        input{
            border-radius: 15px;
            width: 70px;
            height: 20px;
            padding: 4px 9px;
            border: none;
            background-color: #F3F3F3;
        }
        .text{
            color: #818181;
            font-family: Roboto;
            font-size: 1.4rem;
            font-weight: 500;
            letter-spacing: 0.63px;
            margin-bottom: 8px;
        }
        .desc{
            color: #9B9B9B;
            font-family: Roboto;
            font-size: 1.1rem;
            font-weight: 500;
            letter-spacing: 0.85px;
        }
    }
}