.navigation{
    background-color: white;
    padding: 4px 50px;
    border-radius: 30px;
    color: var(--main_black);
    min-width: 486px;
    width: fit-content;
    margin: 0 auto;
    margin-top: 39px;
    margin-bottom: 60px;
    font-family: 'Roboto';
    font-weight: 500;
    letter-spacing: 0.204rem;
        ul{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            gap: 50px;
            li{
                cursor: pointer;
                list-style: none;
                padding: 15px 52px;
                border-radius: 30px;
                text-transform: uppercase;
                font-size: 1.8rem;
                transition: color 0.5s, background-color 0.5s;
                &.active{
                    color: white;
                    background: var(--main_black);
                }
            }
        }
}