@font-face {
    font-family: 'Roboto';
    font-weight: 700;
    src: url(./fonts/Roboto-Bold.ttf);
}
@font-face {
    font-family: 'Roboto';
    font-weight: 500;
    src: url(./fonts/Roboto-Medium.ttf);
}
@font-face {
    font-family: 'Roboto';
    font-weight: 400;
    src: url(./fonts/Roboto-Regular.ttf);
}
:root{
    --main_black: #2E2E2E;
    --main_grey: #F3F3F3;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, body{
    background-color: #e7e7e7;
    font-size: 10px;
}
.wrap{
    max-width: 1238px;
    margin: 0 auto;
}