.wrap{
    .select{
        border: none;
        border-radius: 15px;
        background: var(--main_grey);
        padding: 4px 18px;
        color: var(--main-color-two, #2E2E2E);
        font-family: Roboto;
        font-size: 1.4rem;
        font-weight: 500;
        letter-spacing: 0.85px;
        appearance: none;
        width: 140px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        .title, .option{
            font-family: Roboto;
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 0.85px;
            color: var(--main-color-two);
        }
        .title{
            color: #000000;
            font-weight: 700;
        }
    }
}