.dropdownlist{
    display: flex;
    gap: 40px;
    justify-content: space-between;
    width: fit-content;
    background-color: white;
    padding: 10px 21px;
    border-radius: 15px;
}
.switchSection{
    display: flex;
    background: white;
    padding: 14px;
    border-radius: 15px;
    gap: 5px;
    width: fit-content;
    height: fit-content;
    &__left{
        padding: 10px 0 0 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        .title{
            color: #2E2E2E;
            font-family: Roboto;
            font-size: 1.4rem;
            font-weight: 500;
        }
        .subtitle{
            color: #9B9B9B;
            font-family: Roboto;
            font-size: 1.1rem;
            font-weight: 500;
        }
    }
    &__right{
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
    }
    .img{
        padding: 18px 10px;
        border-radius: 15px;
        background-color: var(--main_grey);
        width: 75px;
        height: 75px;
    }
    input[type='checkbox']{
    }
}
.main{
    border: 1px solid var(--main_black);
    border-radius: 0px 17px 17px 17px;
    padding: 17px 48px 38px;
    max-width: 910px;
    &__wrap{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 10px;
    }
    &__desc{
        margin-bottom: 28px;
        .text{
            font-family: Roboto;
            font-size: 20px;
            font-weight: 600;
            letter-spacing: 0.6px;
        }
        hr{
            max-width: 370px;
        }
    }
    &__left, &__right{
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        flex-direction: column;
    }
}