.wrap{
    border-radius: 15px;
    border: 1px solid #ffffff;
    background: #FFF;
    padding: 18px 31px ;
    width: fit-content;
    max-width: 432px;
    width: 100%;
    &__item:first-child{
        display: flex;
        flex-wrap: wrap;
        gap: 80px;
    }
    &__item:last-child{
        margin-top: 16px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 10px;
        label{
            input{
                display: none;
            }
            input:checked ~ img{
                background-color: #ffffff;
                border: 1px solid var(--main_black);
            }
            img{
                max-width: 76px;
                padding: 12px;
                background-color: #F1F1F1;
                border: 1px solid #F1F1F1;;
                border-radius: 15px;
                transition: border 1s, background-color 0.5s;
            }
        }
    }
    &:hover{
        border-color:var(--main_black);
    }
    .image{
        max-width: 70px;
    }
    .title{
        color: var(--main_black);
        font-family: Roboto;
        font-size: 20px;
        font-weight: 600;
        letter-spacing: 0.6px;
        margin-bottom: 14px;
    }
    .sabtitle{
        color: #818181;
        font-family: Roboto;
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 0.63px;
        margin-bottom: 8px;
    }
    .switcher{
        display: flex;
        justify-content: space-between;
        input{
            width: 12px;
            height: 12px;
            margin-right: 6px;
        }
        label{
            color: #000;
            font-family: Roboto;
            font-size: 1.3rem;
            font-weight: 500;
            letter-spacing: 0.136rem;
        }
    }
}
.mini{
    padding: 4px 10px;
        .wrap__item:first-child{
            .title{
                margin: 0 auto 5px;
            }
        }
        .wrap__item:last-child{
            margin: 0;
            gap: 0;
        }
}